.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-setting-layoutMode img {
  border-radius: 5px;
  padding: 5px;
  box-shadow: 0 3px 8px 0 var(--shadow-color);
}
.RA-setting-layoutMode.RA-setting-layoutMode-disabled img {
  cursor: not-allowed;
}
.RA-setting-layoutMode .RA-setting-selectedIcon {
  top: 62px !important;
}
